import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  ReanderSelect,
} from "../../../components/helpers/field";

class HeadFormKirimBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-3">
          <Field
            name="type"
            component={ReanderSelect}
            options={[
              { value: "Type01", name: "Type 01" },
              { value: "Type02", name: "Type 02" },
              { value: "Type03", name: "Type 03" },
              { value: "Type04", name: "Type 04" },
            ]}
            label="Kode Gudang"
            placeholder="Pilih Gudang"
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="type"
            component={ReanderSelect}
            options={[
              { value: "Type01", name: "Type 01" },
              { value: "Type02", name: "Type 02" },
              { value: "Type03", name: "Type 03" },
              { value: "Type04", name: "Type 04" },
            ]}
            label="Kode Toko"
            placeholder="Pilih Kode Toko"
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="type"
            component={ReanderSelect}
            options={[
              { value: "Type01", name: "Type 01" },
              { value: "Type02", name: "Type 02" },
              { value: "Type03", name: "Type 03" },
              { value: "Type04", name: "Type 04" },
            ]}
            label="Kode Baki"
            placeholder="Pilih Kode Baki"
          />
        </div>
        <div className="col-lg-3">
          <div className="text-right">
            <label>&nbsp;</label>
            <button className="btn btn-primary btn-block" >
              Kirim Data Barang <i className="fa fa-paper-plane ml-3"></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

HeadFormKirimBarang = reduxForm({
  form: "HeadFormKirimBarang",
  enableReinitialize: true,
})(HeadFormKirimBarang);
export default connect()(HeadFormKirimBarang);
